                        transition="scale-transition"
                        offset-y
                        max-width="290px"
                        min-width="auto"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="dateFormatted"
                            label="Data de Contratao"
                            prepend-icon="mdi-calendar"
                            v-bind="attrs"
                            @blur=" dataContratacao = parseDate(dateFormatted)"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="dataContratacao"
                          locale="pt-br"
                          no-title
                          @input="menu1 = false"
                        ></v-date-picker>
                      </v-menu>

                    </v-col>
                    <v-col
                      cols="6"
                      sm="4"
                      md="2"
                      lg="2"
                      xs="2"
                      class="mt-7"
                    >
                      <v-text-field
                        label="Registro"
                        v-model="reg"
                        readonly
                      >
                      </v-text-field>
                    </v-col>
                  </v-row>
                  <v-row class="px-5 py-2">
                    <v-col
                      cols="12"
                      md="12"
                      lg="12"
                      sm="12"
                      xs="12"
                    >
                      <v-card class="color-card">
                        <v-row class="px-5">
                          <v-col
                            cols="12"
                            sm="12"
                            md="12"
                            lg="12"
                            xs="12"
                            class="pt-5 pb-0"
                          >
                          <h4>Informaes do Colaborador:</h4>
                          </v-col>
                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                            lg="6"
                            xs="6"
                            class="pt-5"
                          >
                            <span>Nome Colaborador</span>
                            <v-text-field
                                v-model="nomeColaborador"
                                :rules="[rules.required]"
                                type="nome"
                                solo
                                label="Nome Colaborador"
                                hint="Nome Colaborador"
                              ><!-- :readonly="this.tipoColaborador === 'editar' ? true : false" -->
                            </v-text-field>
                          </v-col>
                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                            lg="6"
                            xs="6"
                            class="pt-5"
                          >
                            <span>Numero de Matricula</span>
                            <v-text-field
                                v-model="numeroMatricula"
                                :rules="[rules.required]"
                                type="matricula"
                                solo
                                label="Numero de Matricula"
                                hint="Numero de Matricula"
                              >
                            </v-text-field>
                          </v-col>
                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                            lg="6"
                            xs="6"
                            class="pt-5"
                          >
                            <span>Email Colaborador</span>
                            <v-text-field
                                v-model="emailColaborador"
                                :rules="[rules.email]"
                                type="email"
                                solo
                                label="Email Colaborador"
                                hint="Email Colaborador"
                              >
                            </v-text-field>
                          </v-col>
                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                            lg="6"
                            xs="6"
                            class="pt-5"
                          >
                            <span>CPF Colaborador</span>
                            <v-text-field
                                v-model="cpfColaborador"
                                :rules="[rules.required]"
                                type="cpf"
                                solo
                                label="CPF Colaborador"
                                hint="CPF Colaborador"
                                v-mask="'###.###.###-##'"
                              >
                            </v-text-field>
                          </v-col>

                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                            lg="6"
                            xs="6"
                            class="pt-5"
                          >
                            <span>Telefone Colaborador</span>
                            <v-text-field
                                v-model="telefoneColaborador"
                                :rules="[rules.required]"
                                type="telefone"
                                solo
                                label="Telefone Colaborador"
                                hint="Telefone Colaborador"
                                v-mask="'(##) #####-####'"
                              >
                            </v-text-field>
                          </v-col>

                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                            lg="6"
                            xs="6"
                            class="pt-5"
                          >
                            <span>Obras</span>
                            <v-autocomplete
                              v-model="modalObra"
                              :rules="[rules.required]"
                              :items="obras"
                              :loading="loadingObras"
                              label="Obras"
                              hint="Obras Ativos"
                              clearable
                              solo
                            ></v-autocomplete>
                          </v-col>

                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                            lg="6"
                            xs="6"
                            class="pt-5"
                          >
                            <span>Funo Colaborador</span>
                            <v-autocomplete
                              v-model="funcaoColaborador"
                              :rules="[rules.required]"
                              :items="funcao"
                              :loading="loadingFuncao"
                              label="Funo Colaborador"
                              hint="Funo Colaborador"
                              clearable
                              solo
                              @change="buscarDiretoIndireto()"
                            ></v-autocomplete>
                          </v-col>

                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                            lg="6"
                            xs="6"
                            class="pt-5"
                          >
                            <span>Direto ou Indireto</span>
                            <v-text-field
                              v-model="modalDiretoIndireto"
                              solo
                              readonly
                            >
                            </v-text-field>
                          </v-col>

                          <v-col
                            cols="12"
                            sm="6"
                            md="6"
                            lg="6"
                            xs="6"
                            class="pt-5"
                          >
                            <span>Horas de Trabalho</span>
                            <v-text-field
                                v-model="modelHorasTrabalho"
                                :rules="[rules.required]"
                                type="horas"
                                solo
                                label="Horas Trabalho"
                                hint="Horas Trabalho"
                              >
                            </v-text-field>
                          </v-col>

                        </v-row>
                      </v-card>
                    </v-col>
                  </v-row>
                  <v-row class="px-5">
                    <v-col
                      cols="12"
                      md="12"
                      lg="12"
                      sm="12"
                      xs="12"
                      class="mt-7 text-right"
                    >
                    <v-btn @click="tipoColaborador === 'editar' ? editarColaborar() : verificarCampos()" color="green" class="mr-3 white--text">Salvar</v-btn>
                    <v-btn @click="returnColaborador()" color="primary">Voltar</v-btn>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card>
            </div>
          </v-flex>
        </v-layout>
      </v-container>
      <v-snackbar
        v-model="snackbar"
        :color="colorSnackbar"
      >
        {{ mensagem }}

        <template v-slot:action="{ attrs }">
          <v-btn
            color="white"
            text
            v-bind="attrs"
            @click="snackbar = false"
          >
            Close
          </v-btn>
        </template>
      </v-snackbar>

      <div v-if="loader">
        <loader object="#ff9633" color1="#ffffff" color2="#17fd3d" size="5" speed="2" bg="#343a40" objectbg="#999793" opacity="80" name="circular"></loader>
      </div>
  </div>
</template>

<script>
import axios from 'axios'
import moment from 'moment'
export default {
  props: ['tipoColaborador', 'colaboradorEdit'],
  mounted () {
    console.log(this.tipoColaborador, this.colaboradorEdit)
    if (this.tipoColaborador === 'editar') {
      console.log(this.tipoColaborador)
      this.reg = this.colaboradorEdit.reg
      this.dataContratacao = (new Date(this.colaboradorEdit.dataContratacao)).toISOString().substr(0, 10)
      this.nomeColaborador = this.colaboradorEdit.nome
      this.emailColaborador = this.colaboradorEdit.email
      this.telefoneColaborador = this.colaboradorEdit.telefone
      this.cpfColaborador = this.colaboradorEdit.cpf
      this.funcaoColaborador = this.colaboradorEdit.funcao
      this.numeroMatricula = this.colaboradorEdit.matricula
      this.modalObra = this.colaboradorEdit.obra
      this.modalDiretoIndireto = this.colaboradorEdit.diretoIndireto
      this.modelHorasTrabalho = this.colaboradorEdit.horasTrabalho
    }
  },
  data: vm => ({
    urlProd: 'https://neexsa.com.br/api_rh/',
    // urlProd: 'http://localhost:4040/api_rh/',
    dataContratacao: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    dateFormatted: vm.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
    menu1: false,
    loader: false,
    mensagem: '',
    snackbar: false,
    colorSnackbar: '',
    funcao: [],
    reg: moment(new Date()).valueOf(),
    // dataCiacao: moment(new Date()).format('L'),
    nomeColaborador: '',
    emailColaborador: '',
    telefoneColaborador: '',
    cpfColaborador: '',
    funcaoColaborador: '',
    loadingFuncao: false,
    numeroMatricula: 0,
    camposVerificados: [],
    obras: [],
    modalObra: '',
    loadingObras: false,
    diretoIndireto: ['Direto', 'Indireto'],
    modalDiretoIndireto: '',
    modelHorasTrabalho: 0,
    funcoes: [],
    rules: {
      required: value => !!value || 'Obrigatrio.',
      email: value => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        return pattern.test(value) || 'Invalid e-mail.'
      }
    }
  }),
  async created () {
    try {
      this.loader = true
      await this.getFuncao()
      await this.getObras()
      this.loader = false
    } catch (err) {
      console.log(err)
      this.loader = false
    }
  },
  computed: {
    computedDateFormatted () {
      return this.formatDate(this.dataContratacao)
    }
  },

  watch: {
    dataContratacao (val) {
      this.dateFormatted = this.formatDate(this.dataContratacao)
    }
  },
  methods: {
    formatDate (dataContratacao) {
      if (!dataContratacao) return null

      const [year, month, day] = dataContratacao.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (dataContratacao) {
      if (!dataContratacao) return null

      const [day, month, year] = dataContratacao.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    returnColaborador () {
      this.$emit('voltar')
    },

    async getFuncao () {
      try {
        this.loadingFuncao = true
        const options = {
          url: `${this.urlProd}dominio/funcao`,
          method: 'GET'
        }
        axios(options)
          .then(res => {
            console.log(res.data)
            this.funcoes = res.data
            this.funcao = res.data.map(x => x.nome)
            this.loadingFuncao = false
          })
          .catch(err => {
            console.log(err)
            this.snackbar = true
            this.mensagem = 'Erro ao carregar Funes'
            this.colorSnackbar = 'red'
            this.loadingFuncao = false
          })
          .finally(() => {
            this.loadingFuncao = false
          })
      } catch (err) {
        console.log(err)
        this.snackbar = true
        this.mensagem = 'Erro ao carregar Funes'
        this.colorSnackbar = 'red'
      }
    },

    async getObras () {
      try {
        this.loadingObras = true
        const options = {
          url: `${this.urlProd}dominio/obras-ativos`,
          method: 'GET'
        }
        axios(options)
          .then(res => {
            console.log(res.data)
            this.obras = res.data
          })
          .catch(err => {
            console.log(err)
            this.snackbar = true
            this.mensagem = 'Erro ao carregar Obras'
            this.colorSnackbar = 'red'
          })
          .finally(() => {
            this.loadingObras = false
          })
      } catch (err) {
        console.log(err)
        this.snackbar = true
        this.mensagem = 'Erro ao carregar Obras'
        this.colorSnackbar = 'red'
      }
    },

    verificarCampos () {
      this.camposVerificados = []
      if (this.nomeColaborador.length < 1) {
        this.camposVerificados.push('Nome Colaborador')
      }

      if (this.numeroMatricula.length < 1 || this.numeroMatricula === 0) {
        this.camposVerificados.push('Numero de Matricula')
      }

      if (this.emailColaborador.length < 1) {
        this.camposVerificados.push('Email Colaborador')
      }

      if (this.cpfColaborador.length < 14) {
        this.camposVerificados.push('CPF Colaborador')
      }

      if (this.telefoneColaborador.length < 15) {
        this.camposVerificados.push('Telefone Colaborador')
      }

      if (this.funcaoColaborador.length < 1) {
        this.camposVerificados.push('Funo Colaborador')
      }

      if (this.modalDiretoIndireto.length < 1) {
        this.camposVerificados.push('Direto ou Indireto')
      }

      if (this.modelHorasTrabalho < 1) {
        this.camposVerificados.push('Horas Trabalho')
      }

      this.salvarColaborar()
    },

    async salvarColaborar () {
      if (this.camposVerificados.length === 0) {
        this.loader = true

        const options = {
          method: 'POST',
          url: `${this.urlProd}novo-colaborador`,
          data: {
            reg: this.reg,
            dataContratacao: new Date(this.dataContratacao).valueOf(),
            nomeColaborador: this.nomeColaborador,
            matricula: this.numeroMatricula * 1,
            emailColaborador: this.emailColaborador ? this.emailColaborador : '',
            telefoneColaborador: this.telefoneColaborador ? this.telefoneColaborador : 0,
            cpfColaborador: this.cpfColaborador ? this.cpfColaborador : 0,
            funcaoColaborador: this.funcaoColaborador ? this.funcaoColaborador : '',
            obra: this.modalObra ? this.modalObra : null,
            diretoIndireto: this.modalDiretoIndireto ? this.modalDiretoIndireto : null,
            horasTrabalho: this.modelHorasTrabalho ? parseInt(this.modelHorasTrabalho) : 0
          }
        }
        try {
          axios(options)
            .then(res => {
              console.log(res)
              if (res.data.qtd) {
                this.snackbar = true
                this.mensagem = res.data.mensagem
                this.colorSnackbar = 'red'
              } else {
                this.snackbar = true
                this.mensagem = res.data.mensagem
                this.colorSnackbar = 'green'
                this.returnColaborador()
              }
              this.loader = false
            })
            .catch(err => {
              console.log(err)
              this.snackbar = true
              this.mensagem = 'Erro ao salvar!!!'
              this.colorSnackbar = 'red'
              this.loader = false
            })
            .finally(() => {
              this.loader = false
            })
        } catch (err) {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao salvar!!!'
          this.colorSnackbar = 'red'
          this.loader = false
        }
      } else {
        this.snackbar = true
        this.mensagem = 'Preencher o(s) campo(s): ' + this.camposVerificados.join(', ')
        this.colorSnackbar = 'red'
      }
    },

    async editarColaborar () {
      this.loader = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}editar-colaborador`,
        data: {
          reg: this.reg,
          dataContratacao: new Date(this.dataContratacao).valueOf(),
          nomeColaborador: this.nomeColaborador,
          matricula: this.numeroMatricula * 1,
          emailColaborador: this.emailColaborador ? this.emailColaborador : '',
          telefoneColaborador: this.telefoneColaborador ? this.telefoneColaborador : 0,
          cpfColaborador: this.cpfColaborador ? this.cpfColaborador : 0,
          funcaoColaborador: this.funcaoColaborador ? this.funcaoColaborador : '',
          obra: this.modalObra ? this.modalObra : null,
          diretoIndireto: this.modalDiretoIndireto ? this.modalDiretoIndireto : null,
          horasTrabalho: this.modelHorasTrabalho ? parseInt(this.modelHorasTrabalho) : 0
        }
      }
      try {
        axios(options)
          .then(res => {
            this.snackbar = true
            this.mensagem = res.data.mensagem
            this.colorSnackbar = 'green'
            this.returnColaborador()
            this.loader = false
          })
          .catch(err => {
            console.log(err)
            this.snackbar = true
            this.mensagem = 'Erro ao salvar!!!'
            this.colorSnackbar = 'red'
            this.loader = false
          })
          .finally(() => {
            this.loader = false
          })
      } catch (err) {
        console.log(err)
        this.snackbar = true
        this.mensagem = 'Erro ao salvar!!!'
        this.colorSnackbar = 'red'
        this.loader = false
      }
    },
    buscarDiretoIndireto () {
      const funcao = this.funcoes.filter(x => x.nome === this.funcaoColaborador)
      const funcaoFilter = funcao.map(w => w.tipo)
      console.log(funcaoFilter)
      this.modalDiretoIndireto = funcaoFilter[0]
      console.log(this.modalDiretoIndireto)
    }
  }
}
</script>

<style lang="scss">
  .color-card {
    background-color: var(--light-gray)!important;
  }
</style>
