<template>
  <div class="colaboradores">
      <v-container v-if="showColaboradores">
        <v-layout row wrap>
          <v-flex sm12 xs12 md12 lg12 class="mt-10">
              <v-card-title class="align-start">
                <v-sheet
                color="brown"
                width="100%"
                class="overflow-hidden mt-n9 transition-swing v-card--material__sheet"
                style="z-index: 2"
                elevation="6"
                max-width="100%"
                rounded
                >
                  <v-theme-provider dark>
                  <slot name="heading" />
                  <div class="py-3">
                    <v-row class="px-5 d-flex">
                      <v-col
                        cols="1"
                        sm="1"
                        md="1"
                        lg="1"
                        xs="1"
                        class="text-left"
                      >
                        <v-btn
                          depressed
                          color="transparent"
                          class="text-left"
                          @click="returnHome"
                        >
                          <v-icon dark small>
                            mdi-arrow-left-bold-circle mdi-36px
                          </v-icon>
                        </v-btn>
                      </v-col>
                      <v-col
                        cols="11"
                        sm="11"
                        md="11"
                        lg="11"
                        xs="11"
                        class="text-center"
                      >
                        <span class="text-h7 white--text  v-card--material__title pr-15">Colaboradores</span>
                      </v-col>
                    </v-row>
                  </div>
                </v-theme-provider>
                </v-sheet>
              </v-card-title>
              <v-card class="card-colaboradores">
                <v-container class="py-0">
                  <v-row class="pt-9 px-5">
                    <template>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="4"
                        xs="4"
                      >
                        <v-text-field
                        v-model="nomePesquisa"
                          clearable
                          label="Pesquisar"
                          append-icon="mdi-magnify"
                        ></v-text-field>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="2"
                        md="2"
                        lg="2"
                        xs="2"
                        style="text-align:right"
                      >
                        <v-switch
                          v-model="switchColaborador"
                          label="Mudar Status"
                        >Status</v-switch>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="2"
                        xs="2"
                        style="text-align:center"
                      >
                        <v-btn color="blue-grey" class="white--text" @click="getColaboradores()">Pesquisar</v-btn>
                      </v-col>
                      <v-col
                        cols="12"
                        sm="4"
                        md="4"
                        lg="2"
                        xs="2"
                        style="text-align:center"
                      >
                        <v-btn color="blue" class="white--text" @click="dialogAdicionarColaboradores = !dialogAdicionarColaboradores">+ Adicionar</v-btn>
                      </v-col>
                    </template>
                  </v-row>
                  <v-row>
                    <template>
                      <v-col
                        cols="12"
                        md="12"
                        lg="12"
                        sm="12"
                        xs="12"
                      >
                        <template>
                          <v-data-table
                            :headers="headers"
                            :items="desserts"
                            :items-per-page="5"
                            class="elevation-1 color-table"
                            :loading="loadingTable"
                          >
                          <template v-slot:[`item.editar`]="{ item }">
                            <v-btn
                              width="30px"
                              height="30px"
                              fab
                              dark
                              small
                              color="primary"
                              @click="editarColaborador(item)"
                            >
                            <v-icon dark small>
                              mdi-pencil
                            </v-icon>{{ item.editar }}
                            </v-btn>
                          </template>
                          <template v-slot:[`item.desativar`]="{ item }">
                            <v-btn
                              width="30px"
                              height="30px"
                              fab
                              dark
                              small
                              :color="item.status === 'ativo' ? 'red' : 'green'"
                              @click="alterarStatusColaborador(item)"
                            >
                            <v-icon dark small>
                              {{item.status === 'ativo' ? 'mdi-close' : 'mdi-play'}}
                            </v-icon>{{ item.editar }}
                            </v-btn>
                          </template>
                          </v-data-table>
                        </template>
                      </v-col>
                      </template>
                  </v-row>
                </v-container>
              </v-card>
          </v-flex>
        </v-layout>

        <v-dialog
          v-model="dialogAdicionarColaboradores"
          transition="dialog-top-transition"
          max-width="600"
        >
          <template>
            <v-card>
              <v-toolbar
                color="primary"
                dark
              >Adicionar Colaboradores</v-toolbar>
              <v-card-text>
                <v-form
                  ref="form"
                >
                    <v-container fluid>
                      <!-- Tab das Telas -->
                      <v-row class="pt-9 px-5 pb-4">
                        <v-tabs
                          v-model="tab"
                          centered
                        >
                          <v-tab>Manual</v-tab>
                          <v-tab>CSV</v-tab>
                        </v-tabs>
                      </v-row>
                      <v-row v-if="tab === 0" style="text-align:center">
                        <v-col
                          cols="12"
                          sm="12"
                          md="12"
                          lg="12"
                          xs="12"
                        >
                          <v-btn color="blue" class="white--text" @click="newColaborador()">+ Adicionar</v-btn>
                        </v-col>
                      </v-row>
                      <v-row v-if="tab === 1">
                        <v-col
                          cols="12"
                          sm="6"
                          md="6"
                          lg="6"
                          xs="6"
                          style="text-align:right"
                        >
                          <v-btn color="blue" class="white--text" @click="baixarModelo()">Download Modelo</v-btn>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                          md="6"
                          lg="6"
                          xs="6"
                          style="text-align:left"
                        >
                          <v-btn color="blue" class="white--text" @click="onPickFile()">+ Adicionar CSV</v-btn>
                          <input type="file" accept=".csv" style="display: none" ref="fileInput" @change="handleFileUpload( $event )"/>
                        </v-col>
                      </v-row>
                    </v-container>
                </v-form>
              </v-card-text>
              <v-card-actions class="justify-end">
                <v-btn
                  text
                  @click="dialogAdicionarColaboradores = false"
                >Fechar</v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>

        <v-dialog
          v-model="dialogColaboradoresCsv"
          transition="dialog-top-transition"
          max-width="1000"
        >
          <template>
            <v-card>
              <v-toolbar
                color="primary"
                dark
              >Verificar Dados</v-toolbar>
              <v-card-text>
                <v-form
                  ref="form"
                >
                    <v-container fluid>
                    <v-row>
                      <v-col
                        cols="12"
                        sm="12"
                      >
                      <v-data-table
                        :headers="headersColbCsv"
                        :items="content.data"
                        :items-per-page="5"
                        class="elevation-1"
                      ></v-data-table>
                      </v-col>
                    </v-row>
                    </v-container>
                </v-form>
              </v-card-text>
              <v-card-actions class="justify-end">
                <v-btn
                  text
                  @click="dialogColaboradoresCsv = false"
                >Fechar</v-btn>
                <v-btn
                  color="green"
                  text
                  @click="sendColabCsv()"
                >Enviar</v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>

        <v-dialog
          v-model="dialogColaboradoresErroCsv"
          transition="dialog-top-transition"
          max-width="1000"
        >
          <template>
            <v-card>
              <v-toolbar
                color="primary"
                dark
              >Colaboradores com Erro</v-toolbar>
              <v-card-text>
                <v-form
                  ref="form"
                >
                    <v-container fluid>
                    <v-row>
                      <v-col
                        cols="12"
                        sm="12"
                      >
                      <v-data-table
                        :headers="headersColbErroCsv"
                        :items="incorretos"
                        :items-per-page="5"
                        class="elevation-1"
                      ></v-data-table>
                      </v-col>
                    </v-row>
                    </v-container>
                </v-form>
              </v-card-text>
              <v-card-actions class="justify-end">
                <v-btn
                  text
                  @click="dialogColaboradoresErroCsv = false"
                >Fechar</v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>

        <v-snackbar
          v-model="snackbar"
          :color="colorSnackbar"
        >
          {{ mensagem }}

          <template v-slot:action="{ attrs }">
            <v-btn
              color="white"
              text
              v-bind="attrs"
              @click="snackbar = false"
            >
              Close
            </v-btn>
          </template>
        </v-snackbar>
      </v-container>
      <div v-if="loader">
        <loader object="#ff9633" color1="#ffffff" color2="#17fd3d" size="5" speed="2" bg="#343a40" objectbg="#999793" opacity="80" name="circular"></loader>
      </div>
      <criar-colaborador
        v-if="showCriarColaborador"
        v-on:voltar="voltarColaborador()"
        :tipoColaborador="tipoColaborador"
        :colaboradorEdit="colaboradorEdit"
      />
    </div>
</template>

<script>
import axios from 'axios'
import Papa from 'papaparse'
import CriarColaborador from './componentes/CriarColaborador.vue'
import moment from 'moment'
import config from '../../../assets/download/config-downloads'
export default {
  components: { CriarColaborador },
  name: 'Colaboradores',
  data: () => ({
    urlProd: 'https://neexsa.com.br/api_rh/',
    // urlProd: 'http://localhost:4040/api_rh/',
    loader: false,
    tipoColaborador: '',
    colaboradorEdit: [],
    showCriarColaborador: false,
    obras: [],
    showColaboradores: true,
    showProjetos: false,
    nomeObra: '',
    novoNomeObra: '',
    snackbar: false,
    mensagem: '',
    colorSnackbar: '',
    nomePesquisa: '',
    switchColaborador: true,
    file: '',
    content: [],
    parsed: false,
    dialogAdicionarColaboradores: false,
    dialogColaboradoresCsv: false,
    dialogColaboradoresErroCsv: false,
    tab: null,
    headers: [
      {
        text: 'Matricula',
        align: 'start',
        sortable: false,
        value: 'matricula'
      },
      {
        text: 'Nome',
        value: 'nome',
        align: 'center'
      },
      {
        text: 'Obra',
        value: 'obra',
        align: 'center'
      },
      {
        text: 'Email',
        value: 'email',
        align: 'center'
      },
      {
        text: 'CPF',
        value: 'cpf',
        align: 'center'
      },
      {
        text: 'Status',
        value: 'status',
        align: 'center'
      },
      {
        text: 'Editar',
        value: 'editar',
        align: 'center'
      },
      {
        text: 'Desativar / Ativar',
        value: 'desativar',
        align: 'center'
      }
    ],

    headersColbCsv: [
      {
        text: 'Data',
        align: 'start',
        sortable: false,
        value: 'Data'
      },
      {
        text: 'Nome',
        align: 'center',
        value: 'Nome'
      },
      {
        text: 'Matricula',
        align: 'center',
        value: 'Matricula'
      },
      {
        text: 'Email',
        align: 'center',
        value: 'Email'
      },
      {
        text: 'CPF',
        align: 'center',
        value: 'CPF'
      },
      {
        text: 'Telefone',
        align: 'center',
        value: 'Telefone'
      }
    ],
    desserts: [],
    headersColbErroCsv: [
      {
        text: 'Data',
        align: 'start',
        sortable: false,
        value: 'dataFormat'
      },
      {
        text: 'Nome',
        align: 'center',
        value: 'Nome'
      },
      {
        text: 'Matricula',
        align: 'center',
        value: 'Matricula'
      },
      {
        text: 'Email',
        align: 'center',
        value: 'Email'
      },
      {
        text: 'Erros',
        align: 'center',
        value: 'Erros'
      }
    ],
    incorretos: [],
    loadingTable: true
  }),
  created () {
    this.getColaboradores()
  },
  methods: {
    returnHome () {
      this.$router.push({ name: 'Home' })
    },
    async getColaboradores () {
      this.loadingTable = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}colaboradores`,
        data: {
          textoPesquisar: this.nomePesquisa ? this.nomePesquisa : '',
          status: this.switchColaborador ? 'ativo' : 'inativo'
        }
      }
      axios(options)
        .then(res => {
          this.desserts = res.data
          console.log(this.desserts)
          this.loadingTable = false
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar Dados'
          this.colorSnackbar = 'red'
          this.loadingTable = false
        })
        .finally(() => {
          this.loadingTable = false
        })
    },
    voltarColaborador () {
      this.showColaboradores = true
      this.showCriarColaborador = false
      this.getColaboradores()
    },
    newColaborador () {
      this.showColaboradores = false
      this.showCriarColaborador = true
      this.dialogAdicionarColaboradores = false
      this.tipoColaborador = 'criar'
    },
    editarColaborador (item) {
      this.colaboradorEdit = item
      this.tipoColaborador = 'editar'
      this.showColaboradores = false
      this.showCriarColaborador = true
    },

    async alterarStatusColaborador (item) {
      console.log(item)
      const options = {
        method: 'POST',
        url: `${this.urlProd}alterar-status-colaborador`,
        data: {
          reg: item.reg,
          status: this.switchColaborador ? 'inativo' : 'ativo'
        }
      }
      axios(options)
        .then(res => {
          this.snackbar = true
          this.mensagem = res.data.mensagem
          this.colorSnackbar = 'green'
          this.getColaboradores()
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao salvar!!!'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
        })
    },

    onPickFile () {
      this.$refs.fileInput.click()
    },

    handleFileUpload (event) {
      this.file = event.target.files[0]
      console.log(this.file)
      this.parseFile()
    },

    parseFile () {
      Papa.parse(this.file, {
        header: true,
        skipEmptyLines: true,
        complete: function (results) {
          this.content = results
          console.log(this.content)
          this.parsed = true
          this.dialogColaboradoresCsv = true
          this.dialogAdicionarColaboradores = false
        }.bind(this)
      })
    },

    sendColabCsv () {
      this.loader = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}novo-colaborador-csv`,
        data: {
          dados: this.content.data
        }
      }
      axios(options)
        .then(res => {
          console.log(res.data.incorretos.length)
          this.dialogColaboradoresCsv = false
          this.getColaboradores()
          if (res.data.incorretos.length > 0) {
            this.incorretos = res.data.incorretos.map(x => {
              return {
                ...x,
                dataFormat: x.Data === '' ? '' : moment(x.Data).format('L'),
                Erros: x.Remove.join()
              }
            })
            this.dialogColaboradoresErroCsv = true
          }
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar Dados'
          this.colorSnackbar = 'red'
          this.loadingTable = false
        })
        .finally(() => {
          this.loader = false
        })
    },

    baixarModelo () {
      const link = document.createElement('a')
      link.download = 'novo_colaboradores.csv'
      link.href = config.templeteModeloColaborador
      link.click()
    }
  }
}
</script>

<style lang="scss" scoped>
  .color-icon {
    color: var(--dark);
  }

.icon-alien {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  .ajuste-sheet{
    margin-top: -54px;
  }

  .card-colaboradores{
    margin-top: -30px;
    z-index: 0;
  }

  .color-row{
    background-color: var(--light-gray)!important;
  }

  .click-projetos {
    cursor: pointer;
  }

  .color-table {
    background-color: var(--light-gray)!important;
  }
</style>
